@import url('https://fonts.googleapis.com/css?family=Montserrat&display=swap');
@import url('https://fonts.googleapis.com/css?family=Satisfy&display=swap');
@import url('https://fonts.googleapis.com/css?family=Dancing+Script&display=swap');
@import url('https://fonts.googleapis.com/css?family=Dancing+Script|Josefin+Sans|Satisfy&display=swap');

/*********** all  **********/

html {
  background-color: #1a1a1a;
}

.App {
  text-align: center;
  color: white;
  /* background-size: cover;
  background-image:url("./assets/sunset-switzerland.png"); */
  background-color: #1a1a1a;
  font-family: 'Josefin Sans', sans-serif;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  align-items: flex-start;
  flex-direction: column;

  font-size: calc(10px + 2vmin);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.underline {
  animation: blink infinite 1s linear;
}

@keyframes blink {
  0% {
    color: #E0EBE8;
  }

  49% {
    color: #E0EBE8;
  }

  50% {
    color: #1a1a1a;
  }

  100% {
    color: #1a1a1a;
  }
}

.Pagination {
  width: 99vw;
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
}

.pagination-arrow {
  color: #9EC6C0;
}

.pagination-arrow-a {
  margin: 7%;
  position: fixed;
  bottom: 0;
  border: none;
  background: none;
}

.pagination-arrow-a:focus {
  outline: none;
  border: none;
}

.pagination-arrow-a-right {
  right: 0;
}

.pagination-arrow-a-left {
  left: 0;
}

.sun {
  width: 14%;
  position: absolute;
}

.cloude {
  width: 10%;
  position: absolute;
}

.windmill-container {
  position: absolute;
  bottom: 0;
}

.windmill-vector {
  bottom: 0;
  position: absolute;
  z-index: 0;
}

.windmill-spin {
  position: absolute;
  z-index: 1;
  animation: App-logo-spin infinite 20s linear;
}

/*********** desktop  **********/
@media only screen and (min-width: 801px) {
  .App-header {
    height: 100vh;
    max-height: 100vh;
  }


  .windmill-vector {
    max-width: 100px;
    left: 77px;
  }

  .windmill-spin {
    max-width: 250px;
    bottom: 54px;
  }
}

/*********** mobile  **********/
@media only screen and (max-width: 800px) {
  .App-header {}

  .windmill-vector {
    max-width: 50px;
    left: 39px;
  }

  .windmill-spin {
    max-width: 125px;
    bottom: 30px;
  }
}