@import url(https://fonts.googleapis.com/css?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Josefin+Sans|Satisfy&display=swap);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Josefin+Sans|Satisfy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*********** all  **********/

html {
  background-color: #1a1a1a;
}

.App {
  text-align: center;
  color: white;
  /* background-size: cover;
  background-image:url("./assets/sunset-switzerland.png"); */
  background-color: #1a1a1a;
  font-family: 'Josefin Sans', sans-serif;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  align-items: flex-start;
  flex-direction: column;

  font-size: calc(10px + 2vmin);
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }

  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.underline {
  -webkit-animation: blink infinite 1s linear;
          animation: blink infinite 1s linear;
}

@-webkit-keyframes blink {
  0% {
    color: #E0EBE8;
  }

  49% {
    color: #E0EBE8;
  }

  50% {
    color: #1a1a1a;
  }

  100% {
    color: #1a1a1a;
  }
}

@keyframes blink {
  0% {
    color: #E0EBE8;
  }

  49% {
    color: #E0EBE8;
  }

  50% {
    color: #1a1a1a;
  }

  100% {
    color: #1a1a1a;
  }
}

.Pagination {
  width: 99vw;
  display: flex;
  justify-content: space-between;
  position: fixed;
  bottom: 0;
}

.pagination-arrow {
  color: #9EC6C0;
}

.pagination-arrow-a {
  margin: 7%;
  position: fixed;
  bottom: 0;
  border: none;
  background: none;
}

.pagination-arrow-a:focus {
  outline: none;
  border: none;
}

.pagination-arrow-a-right {
  right: 0;
}

.pagination-arrow-a-left {
  left: 0;
}

.sun {
  width: 14%;
  position: absolute;
}

.cloude {
  width: 10%;
  position: absolute;
}

.windmill-container {
  position: absolute;
  bottom: 0;
}

.windmill-vector {
  bottom: 0;
  position: absolute;
  z-index: 0;
}

.windmill-spin {
  position: absolute;
  z-index: 1;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

/*********** desktop  **********/
@media only screen and (min-width: 801px) {
  .App-header {
    height: 100vh;
    max-height: 100vh;
  }


  .windmill-vector {
    max-width: 100px;
    left: 77px;
  }

  .windmill-spin {
    max-width: 250px;
    bottom: 54px;
  }
}

/*********** mobile  **********/
@media only screen and (max-width: 800px) {
  .App-header {}

  .windmill-vector {
    max-width: 50px;
    left: 39px;
  }

  .windmill-spin {
    max-width: 125px;
    bottom: 30px;
  }
}
.page1 {
  width: 100%;
  height: 100vh;
  max-height: 100vh;
}

.opening-text {
  display: flex;
  align-self: center;
  justify-content: center;
  flex-direction: row;
  font-family: 'Montserrat', sans-serif;
  font-size: 0.8em;
}

.mainTitle {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 3em;
}

.arrayTitles {
  font-family: 'Josefin Sans', sans-serif;
  font-size: 2.5em;
}

.static-paragraph {
  text-align: center;
}

.hi {
  font-size: 1em;
  text-align: left;
}

.amir-subtitle {
  font-size: 1em;
}

.amir-description {
  font-size: 0.8em;
  text-align: center;
}

/*********** desktop  **********/
@media only screen and (min-width: 800px) {
  .opening-text {
    margin: 5% auto;
    min-height: 22vh;
    min-width: 35%;
  }

  .static-paragraph {
    margin: 10px;
  }
}

/*********** mobile  **********/
@media only screen and (max-width: 800px) {
  .opening-text {
    margin: 20% 10%;
    min-height: 22vh;
    min-width: 35%;
  }

  .static-paragraph {
    margin: 56% 10%;
  }
}
.page2 {
  margin: 1.5em 18%;
  align-items: baseline;
}

.paragraph {
  text-align: justify;
  font-family: 'Josefin Sans', sans-serif;
}

.paragraph-title {
  font-size: 0.7em;
  line-height: 1em;
  margin-top: 25px;
}

.paragraph-date {
  color: gray;
  font-style: italic;
  font-size: 0.5em;
  line-height: 0;
}

.paragraph-description {
  font-size: 0.6em;
  line-height: 1em;
}

.main-titles {
  font-size: 1em;
  line-height: 1em;
  text-align: justify;
}

.brake {
  height: 10px;
}


/*********** desktop  **********/
@media only screen and (min-width: 700px) {
  .page2 {
    display: flex;
  }

  .row {
    width: 50%;
  }

  .paragraph {
    width: 80%;
  }
}

